.userRow {
  /* background-color: #000227; */
  /* background-color: #57656a; */
  /* border-radius: 10px;
    width: 60%;
    margin-top: 1%;
    margin-bottom: 1%; */
  width: 100%;
  padding: 24px;
  background-color: var(--chat-background-color);
  box-shadow: none;
}

.mionRow {
  /* background-color: #060930; */
  /* background-color: #57656a;
    border-radius: 10px;
    width: 60%; */
  max-width: 100%;
  padding: 24px;
  background-color: var(--chat-background-color);
  box-shadow: none;
}

.userFlex {
  /* margin-left: 200px;
    margin-right: 200px; */
  display: flex;
  flex-direction: row;
  /* align-items: center; */
  justify-content: flex-start;
}

@media screen and (min-width: 1000px) {
  .userFlex {
    justify-content: flex-end;
  }
}

.sideBarClose.userFlex {
  justify-content: flex-start;
}

.mionFlex {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  /* max-width: 70%; */
}

.userAvatar {
  width: 50px;
  /* width: 10%; */
  height: 50px;
  background-color: var(--chat-message-text-color);
  /* text-align: center; */
  border-radius: 10px;
  /* margin-right: 50px;     */
  margin-right: 1%;
}

.mionAvatar {
  width: 50px;
  height: 50px;
  /* background-color: lightgreen; */
  /* text-align: center; */
  border-radius: 10px;
  /* margin-right: 50px; */
  margin-right: 1%;
}

.mionAvatar img {
  width: 50px;
}

.textContainer {
  max-width: 70%;
  background-color: var(--sidebar-text-color);
  padding: 1% 2%;
  border-radius: 10px;
  border: 1px solid var(--text-container-border-color);
}

.text {
  /* color: #CACACA; */
  /* color: #000227; */
  color: var(--chat-message-text-color);
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  margin-right: 40px;
}

.userText {
  max-width: 70%;
  /* background: linear-gradient(0deg, rgba(0, 120, 210, 0.3), rgba(0, 120, 210, 0.3)); */
  background: var(--user-message-color);
  /* padding: 1% 2%; */
  padding: 12px 0 12px 12px;
  border-radius: 8px 8px 0 8px;
  display: flex;
  flex-direction: row;
  position: relative;
  flex-wrap: wrap;
}

.mionText {
  max-width: 70%;
  /* padding: 1% 2%; */
  padding: 12px 0 12px 12px;
  border-radius: 8px 8px 8px 0;
  background: var(--mion-message-color);
  display: flex;
  flex-direction: row;
  position: relative;
}

.timestamp {
  position: absolute;
  bottom: 5px;
  right: 5px;
}

.timestampText {
  color: var(--sidebar-text-color);
}

.tableContainer {
  max-width: 100%;
}

.mionLoading{
  width: 50%;    
  padding: 20px;
  border-radius: 8px 8px 8px 0;
  background: var(--mion-message-color);
  display: flex;
  flex-direction: row;
  position: relative;
  margin: 24px;
}
.fileMessage{
  min-width: 200px;
  width: 100%;
}
.iconDoc{
  float: left;
  margin-right: 10px;
}
.fileName{
  font-size: 14px;
  color: var(--chat-message-text-color);
  font-weight: 400;
}
.fileSize{
  font-size: 14px;
  color: var(--cabinet-subtext-color);
  font-weight: 400;
}