.fileUpload{
  width: calc(100% - 48px);
  position: absolute;
  background-color: var(--mion-message-color);
  margin: 0 24px;
  bottom: 60%;
  border-radius: 8px;
  padding: 12px;
  padding-bottom: 30px;
}
.iconDoc{
  float: left;
  margin-right: 8px;
}
.fileName{
  font-size: 14px;
  color: var(--chat-message-text-color);
  font-weight: 400;
}
.deleteButton{
  cursor: pointer;
  outline: none;
  border: none;
  background: transparent;
  margin-left: 8px;
}
.iconTrash{
  color: var(--cabinet-subtext-color);
  width: 14px;
  height: 12px;
}
.fileSize{
  font-size: 14px;
  color: var(--cabinet-subtext-color);
  font-weight: 400;
}