.logoBlock{
  display: flex;
  cursor: pointer;
}
.logoBlock svg {
  color: var(--color-logo)
}
.O{
  display: inline-block;
  margin: 0 5px;
}
.logoBlock .O svg {
  transition: color .3s;
}
.logoBlock:hover .O svg{
  color: var(--color-logo-hover);
}
.slogan{
  color: var(--color-logo-slogan);
  margin-top: 5px;
  margin-left: -5px;
  font-family: 'Inter';
  font-style: normal;
  font-size: 10px;
  font-weight: 400;
}
.ReactLogo1, .N, .slogan{
  transition: width .3s;
}
.logo.sideBarClose .ReactLogo1, 
.logo.sideBarClose .N,
.logo.sideBarClose .slogan{
  width: 0;
  opacity: 0;
}
