*{
   box-sizing: border-box;
   padding: 0;
   margin: 0;
}
html, body, #root, #root>.Theme{
   height: 100%;
   color: var(--cabinet-subtext-color);
   scroll-behavior: smooth;   
}
.Layout{
   background-color: var(--mion-background-color);
}
.ListBox{
   background-color: var(--mion-background-color);
}
.TableCell{
   background-color: var(--mion-message-color)!important;
}

ol{
   margin: 10px;
   padding: 5px;
}

li{
   margin: 5px;
}
.apexcharts-tooltip {
   background: var(--mion-message-color)!important;
   color: var(--chat-message-text-color)!important;
}
.slideActive{
   filter: grayscale(0);
}
.menuItemActive{
   color: #135AFF;
}
.videoNotActive{
   filter: grayscale(90%);
}