.startScreen{
  margin: 7% 9%;
}
@media (max-width: 1600px) {
  .startScreen {
    margin: 5%;
  }
}
.li{
  list-style: none;
  margin-bottom: 32px;
}
@media (max-width: 1600px) {
  .li {
    margin-bottom: 20px;
  }
}
.logo{
  float: left;
  color: var(--mion-text-color);
  margin-right: 24px;
  width: 40px;
  height: 40px;
  margin-top: -5px;
}
@media (max-width: 1600px) {
  .logo {
    width: 20px;
    height: 20px;
    margin-right: 10px;
  }
}
.title {
  color: var(--mion-text-color);
  font-weight: 600;
  font-size: 24px;
} 

@media (max-width: 1600px){
  .title{
    color: var(--mion-text-color);
    font-weight: 600;
    font-size: 16px;
  }
}

.text{
  margin: 12px 0;
  color: var(--mion-text-color);
  font-size: 16px;
  font-weight: 400;
  margin-left: 95px;
}
@media (max-width: 1600px) {
  .text {
    font-size:12px;
    margin-left: 50px;
  }
}
.greyText{
  color: var(--cabinet-subtext-color);
  font-size: 14px;
  font-weight: 400;
  margin-top: 24px;
  margin-left: 95px;
}
@media (max-width: 1600px) {
  .greyText {
    font-size: 10px;
    margin-left: 50px;
  }
}
.link{
  color: var(--color-link);
  text-decoration: none; 
}
.line{
  margin-right: 12px;
}
@media (max-width: 1600px) {
  .line {
    margin-right: 6px;
  }
}
.infoBlocks{
  display: flex;
  margin: 32px -8px;
  justify-content: space-between;
}
@media (max-width: 1600px) {
  .infoBlocks {
    display: flex;
    margin: 20px -8px;
    justify-content: space-between;
  }
}
.infoBlock{
  background: var(--mion-message-color);
  padding: 12px;
  width: calc(33% - 16px);
  margin: 0 8px;
  height: 134px;
  position: relative;
  color: var(--cabinet-subtext-color);
  border-radius: 4px;
  cursor: pointer;
  border: 0;
  outline: none;
  text-align: left;
  flex-direction: column;
  display: flex;
  font-size: 16px;
}
.arrow{
  position: absolute;
  bottom: 12px;
  right: 12px;
  color: var(--cabinet-subtext-color);
}
.askName{
  margin-bottom: 12px;
  font-size: 16px;
  color:var(--mion-text-color);
}
.askText{
  color: var(--cabinet-subtext-color);
  font-size: 14px;
}