.menu {
  width: 100%;
  height: 100%;
  /* height: 464px; */
  /* background-color: #000227; */
  /* background-color: #FFFFFF; */
  font-family: Inter, serif;
  font-weight: 400;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
}

.menuItems {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 80%;
  margin-left: -35px;
  margin-bottom: 64px;
}

.menuItems.menuItemsSideBarClosed {
  margin-left: 0;

}

.menuItem {
  /* height: 75px; */
  /* padding-left: 60px; */
  display: flex;
  flex-direction: row;
}

hr {
  /* margin-bottom: 20px; */
  width: 100%;
  margin: 0;
}

.icons {
  /* color: var(--main-background-color); */
}

.btn {
  width: 100%;
  margin-top: 2%;
  margin-bottom: 2%;
}

.btn span {
  text-align: left;
  /* color: var(--sidebar-text-color); */
}

.btn2Flex {
  display: flex;
  flex-direction: row;
  width: 80%;
  margin-top: 2%;
  margin-bottom: 2%;
}

.btn2 {
  text-align: left;
  width: 100%;
  /* color: var(--sidebar-text-color); */
}

.user {
  width: 70%;
  margin-bottom: 64px;
  cursor: pointer;
  margin-left: -26px;
}

.user.userSideBarClosed {
  margin-left: 0;
  margin-bottom: 136px;
}

@media (max-width: 1600px) {
  .user.userSideBarClosed {
    margin-left: 0;
    margin-bottom: 60px;
  }
}

.avatar {
  display: flex;
  flex-direction: row;
  margin-bottom: 15%;
  cursor: pointer;
  width: 70%;
}

.avatarText {
  margin-left: 12px;
}

.avatarName {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  /* color: var(--chat-message-text-color); */
}

.avatarSubName {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 110%;
  /* color: var(--sidebar-text-color); */
}