.cabinet {
  height: 100%;
  width: 100%;
  background-color: var(--mion-background-color);
}

.topBarrier {
  height: 10%;
  background-color: var(--mion-background-color);
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
}

.title {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 150%;
  color: var(--mion-text-color);
  margin-bottom: 32px;
}

.cards {
  height: 89%;
  /* padding-bottom: 32px; */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.profileCard {
  background: var(--chat-background-color);
  border-radius: 4px;
  width: 60%;
  /* height: 20%; */
  padding: 36px 48px 32px 36px;
}

.avatarFlex {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.avatar {
  width: 48px;
  border-radius: 24px;
  border: 2px solid #0078D2;
}

.iconEdit {
  color: var(--cabinet-subtext2-color);
}

.filesCard {
  width: 60%;
  border: 1px solid var(--cabinet-files-border-color);
  border-radius: 4px;
  padding: 32px;
  /* margin-bottom: 3%; */
}

.fileEllipse {
  width: 40px;
  height: 40px;
  border-radius: 20px;
  background-color: var(--cabinet-ellipse-color);
  display: flex;
  justify-content: center;
}

.fileIcon {
  width: 16px;
}

.mainText {
  margin-top: 32px;
  color: var(--mion-text-color);
}

.subText {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  color: var(--cabinet-subtext-color);
  margin-top: 16px;
}

.subText2 {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  color: var(--cabinet-subtext2-color);
  margin-top: 16px;
}

.btn {
  margin-top: 32px;
  display: flex;
  justify-content: flex-end;
}