.sideBar {
  display: flex;
  flex-direction: column;
  position: relative;
  padding: 32px 38px;
  justify-content: space-between;
  transition: width .3s;
  height: 100%;
}
.sideBarContainer{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: calc(100% - 47px);
}
@media screen and (min-width: 1000px) {
  .sideBar {
    width: 376px;
  }
}

.sideBar.sideBarClosed {
  width: 136px;
}

.contentMenu{
  margin-top: 64px;
}
.chatLink{
  background: transparent;
  border: none;
  display: flex;
}
.toggleSideBar{
  background: transparent;
  border: none;
  outline: none;
  color: var(--cabinet-subtext2-color);
  width: 40px;
  height: 40px;
  position: absolute;
  bottom: 100px;
  right: 0;
  cursor: pointer;
  transform: rotate(180deg);
}

@media (max-width: 1600px) {
  .toggleSideBar {
    bottom: 30px;
  }
}

.sideBar.sideBarClosed .toggleSideBar{
  right: calc(50% - 22px);
  transform: rotate(0);
}
