:root {
    /* --main-background-color: #FFFFFF; */
    --main-background-color: #000227;
    /* --chat-background-color: #F7F8FA; */
    --chat-background-color: #222336;
    /* --user-message-color: linear-gradient(0deg, rgba(0, 120, 210, 0.3), rgba(0, 120, 210, 0.3)); */
    --user-message-color: linear-gradient(0deg, rgba(0, 2, 39, 0.08), rgba(0, 2, 39, 0.08)), #2B4578;
    --mion-message-color: #353648;
    /* --chat-message-text-color: #000227; */
    --chat-message-text-color: #FFFFFF;
    /* --sidebar-text-color: #57656A; */
    --sidebar-text-color: rgba(255, 255, 255, 0.6);
    --btn-background-color: #060930;
    --text-container-border-color: #CACACA;
    --cabinet-title-color: #FFFFFF;
    --cabinet-subtext-color: #66677D;
    --cabinet-subtext2-color: #999AA9;
    --cabinet-profile-background-color: #222336;
    --cabinet-files-border-color: #494A5E;
    --cabinet-ellipse-color: #284072;
}
.Theme_color_gpnDark {
    --color-logo: #ffffff;
    --color-logo-hover: #0078D2;
    --color-logo-slogan: #999AA9;
    --mion-background-color: #272B34;
    --mion-text-color: #ffffff;
    --cabinet-profile-background-color: #222336;
    --cabinet-subtext-color: #66677D;
    --cabinet-subtext2-color: #707F8D;
    --cabinet-files-border-color: #494A5E;
    --cabinet-ellipse-color: #284072;
    --chat-background-color: #2F333E;
    --mion-message-color: #3A3F4B;
    --user-message-color: linear-gradient(0deg, rgba(0, 2, 39, 0.08), rgba(0, 2, 39, 0.08)), #385F89;
    --chat-message-text-color: #FFFFFF;
    --sidebar-text-color: rgba(255, 255, 255, 0.6);
    --color-link: #0078D2;
    --decor-color: #3F8AE2;
}
.Theme_color_gpnDefault {
    --color-logo: #000227;
    --color-logo-hover: #0078D2;
    --color-logo-slogan: #66677D;
    --mion-background-color: #ffffff;
    --mion-text-color: #232323;
    --cabinet-profile-background-color: #F2F4F5;
    --cabinet-subtext-color: #66677D;
    --cabinet-subtext2-color: #707F8D;
    --cabinet-files-border-color: #E6E7EA;
