.musicPlayer {
  background-color: var(--mion-background-color);
}

.topBarrier {
  height: 10%;
  background-color: var(--mion-background-color);
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
}

.title {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 150%;
  color: var(--mion-text-color);
  margin-bottom: 32px;
}

