.btn {
  color: var(--sidebar-text-color);
}

.controls {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.playerCard {
  width: 60%;
  border: 1px solid var(--cabinet-files-border-color);
  border-radius: 4px;
  padding: 32px;
}

.slider {
  margin-left: 8px;
  margin-right: 8px;
}