.mainLayout {
  height: 100%;
  width: 100%;
}

.mainHeader {
  height: 96px;
}

.mainContainer {
  height: 100%;
  flex-shrink: 2;
  flex-grow: 1;
  width: calc(100% - 376px);
}
.sideBarClosed .mainContainer {
  width: calc(100% - 136px);
}

.sideBarWrapper {
  width: 376px;
}

.sideBarClosed .sideBarWrapper {
  width: 136px;
}

.contentWrapper {
  width: 100%;
  height: calc(100% - 96px);
  padding: 32px;
}