.menuLink{
  background: transparent;
  border: 2px transparent solid;
  display: flex;
  align-items: center;
  height: 40px;
  color: var(--cabinet-subtext2-color);
  font-size: 16px;
  font-family: Inter;
  font-weight: 400;
  transition: color .3s, border .3s;
  cursor: pointer;
}
.icon{
  margin: 0 20px 0 16px;
}
.menuLink.isActive, .menuLink:hover{
  border-left: 2px #3F8AE2 solid;
  color: var(--mion-text-color);
}
