.documentPage {
  position: relative;
  max-width: 528px;
}

.tabs {
  display: flex;
  margin: 0 -4px;
}

.tab {
  padding: 8px 12px;
  border: none;
  outline: none;
  background: var(--cabinet-profile-background-color);
  margin: 0 4px;
  color: var(--cabinet-subtext2-color);
  cursor: pointer;
  transition: color .3s;
  border-radius: 4px;
  font-size: 16px;
}

.tab:hover,
.activeTab {
  color: var(--decor-color);
}

.tabsContent {
  margin-top: 64px;
}

.documentItem {
  margin-bottom: 32px;
  display: flex;
  align-items: flex-start;
}

.defaultName {
  font-weight: 400;
  font-size: 16px;
  color: var(--cabinet-subtext2-color)
}

.defaultName p {
  margin-bottom: 32px;
}

.document {
  display: flex;
  align-items: flex-start;
}

.docType {
  margin-right: 12px;
}

.docTitle {
  color: var(--chat-message-text-color);
  font-size: 16px;
  font-weight: 400;
  margin-bottom: 8px;
}

.docSize {
  color: var(--cabinet-subtext2-color);
  font-size: 12px;
  font-weight: 400;
  margin-bottom: 12px;
}

.docDate {
  background: var(--cabinet-profile-background-color);
  padding: 5px 8px;
  border-radius: 12px;
  background: var(--cabinet-profile-background-color);
  margin-bottom: 32px;
  display: inline-block;
  font-size: 10px;
}

.docFile {
  background: var(--decor-color);
  color: #ffffff;
  font-size: 12px;
  font-weight: 600;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  width: 65px;
  height: 25px;
  border-radius: 20px;
  cursor: pointer;
}

.tabInfo {
  overflow: auto;
  height: calc(100vh - 350px);
}