.container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
}

.btn {
  /* margin-top: 4%; */
  width: 100%;
  /* color: var(--main-background-color);
  border-color: var(--main-background-color);
  background-color: var(--btn-background-color); */
  font-family: Inter, serif;
  font-weight: 400;
}

.btn span {
  text-align: left;
}


.chatsItem {
 margin-bottom: 24px;
}

.chatItemBtn {
  width: 100%;
  /* color: var(--sidebar-text-color); */
  margin-top: 2%;
  margin-bottom: 2%;
}

.chatItemBtn span {
  text-align: left;
}

.icons {
  color: var(--main-background-color);
}

.toggleSideBar {
  position: absolute;
  left: calc(100% - 32px);
  top: 0;
  display: block;
}

@media screen and (max-width: 1000px) {
  .toggleSideBar {
    display: none;
  }
}