.errorBlock{  
  padding: 30px;
  background: #FB8B28;
  margin: 0 auto;
  color: #FFFFFF;
  font-size: 16px;
}
.errorWrapper{
  position: fixed;
  top: 0;
  display: flex;
  justify-content: center;
  width: 100%;
}

.errorButton{
  margin-left: 28px;
  background-color:rgba(255, 255, 255, 0.4);
  outline: none;
  border: none;
  padding: 8px 12px;
  cursor: pointer;
  color: #FFFFFF;
}