.landing{
  font-size: 14px;
  font-weight: 500;
  color: #232323;
  width: 100%;
  margin: 0 auto;
  position: relative;
  font-family: 'Montserrat';
  padding-top:120px;
}
@media (min-width:1340px) {
  .landing {
    font-size: 16px;
  }
}
.container{
  width: 100%;
  margin: 0 auto;  
  max-width: 1000px;
  padding: 0 60px;
}
@media (min-width:1340px) {
  .container {
    width: 100%;
    margin: 0 auto;
    max-width: 1340px;
    padding: 0 60px;
  }
}
@media (min-width:1440px) {
  .container {
    width: 100%;
    margin: 0 auto;
    max-width: 1440px;
    padding: 0 60px;
  }
}
@media (min-width:1920px){
  .container {
    width: 100%;
    margin: 0 auto;
    max-width: 1920px;
    padding: 0 60px;
  }
}
.header{
  height: 120px;
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 9999;
  background: #ffffff;
}
.headerContainer{
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.logo svg{
  width: 80px;
  height: 56px;
  color: #135AFF;
}
.menuItem{
  margin: 0 30px;
  font-size: 14px;
  font-weight: 600;
  color: #232323;
  font-family: 'Montserrat';
  text-decoration: none;
  transition: .3s color;
}
@media (min-width:1340px) {
  .menuItem {
    font-size: 16px;
  }
}
.menuItem:hover{
  color: #135AFF;
}
.startButton{
  padding: 16px 24px;
  background: #232323;
  transition: .3s background;
  cursor: pointer;
  color: #FFFFFF;
  font-size: 14px;
  font-weight: 600;
  font-family: 'Montserrat';
  text-decoration: none;
  border-radius: 12px;
}
@media (min-width:1340px) {
  .startButton {
    font-size: 16px;
  }
}
.startButton:hover {
  background: #135AFF;
}
.mainBlockTop{
  display: flex;
  height: 816px;
  width: 100%;
  position: relative;
  margin-bottom: 80px;
}
.mainBlockTopLeft{
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-right: 70px;
  
}
.subTitle{
  color:#AEB3B9;
  font-size: 14px;
  margin-bottom: 48px;
  letter-spacing: 2px;
  font-family: 'Montserrat';
  font-weight: 700;
  text-transform: uppercase;
}
@media (min-width:1340px) {
  .subTitle {
    font-size: 16px;
  }
}
.title{
  color: #232323;
  font-size: 48px;
  font-family: 'Montserrat';
  font-weight: 700;
  margin-bottom: 48px;
}
@media (min-width:1340px) {
  .title {
    font-size: 64px;
  }
}
.description{
  font-size: 16px;
  font-weight: 500;
  color: #232323;
}
@media (min-width:1340px) {
  .description {
    font-size: 18px;
  }
}
.mainBlockTopRight{
  width: 50%;
  position: relative;
  padding: 150px 60px;
}
.mainBlockBackground{
  position: absolute;
  top: 0;
  left: 0;
  height: 816px;
  border-radius: 25px;
  filter: grayscale(0);
  transition: .8s filter;
}

.interface{
  z-index: 999;
  position: relative;
}
.mainBlockBottom{
  border-top: #D5D8DB 1px solid;
  padding-top: 160px;
}
.titleSmall{
  font-family: 'Montserrat';
  font-weight: 500;
  font-size: 32px;
}
@media (min-width:1340px) {
  .titleSmall {
    font-size: 48px;
  }
}
.mainBlockFlex{
  display: flex;
}
.mainBlockBottomLeft{
  margin-right: 60px;
}
.descriptionP{
  margin: 20px 0;
}
.directionsSection{
  margin-top: 160px;
}
.slide{
  padding: 160px 0;
  background-image: url('./images/background-slider.png');
  background-repeat: no-repeat;
  background-size: cover;
  filter: grayscale(100%);
  position: sticky;
  top: 0;
  transition: filter 1s;
}
.slideName{
  color: #FFFFFF;
  font-size: 16px;
  font-weight: 700;
  font-family: 'Montserrat';
  margin-bottom: 48px;
  letter-spacing: .1rem;
  text-transform: uppercase;
}
.slideText{
  font-family: 'Montserrat';
  font-weight: 500;
  font-size: 32px;
  color: #FFFFFF;
}
@media (min-width:1340px) {
  .slideText {
    font-size: 48px;
  }
}
.aboutSection{
  margin-top: 160px;
}
.aboutBlockFlex{
  display: flex;
  padding-bottom: 80px;
  border-bottom: #D5D8DB 1px solid;
}
.aboutBlockLeft{
  width: calc(50% - 30px);
  margin-right: 30px;
}
.aboutBlockRight{
  width: calc(50% - 30px);
  margin-left: 30px;
}
.columnTitle{
  font-family: 'Montserrat';
  font-weight: 500;
  font-size: 28px;
  color: #232323;
}
@media (min-width:1340px) {
  .columnTitle {
    font-size: 32px;
  }
}
.mediaBlock{
  margin: 160px -30px;
  position: relative;
  display: flex;
  justify-content: space-between;
}
.mediaImage{
  margin: 0 30px;
  width: calc(33% - 60px);
}
.historyBlockFlex{
  display: flex;
  padding-bottom: 80px;
  border-bottom: 1px #D5D8DB solid;
}
.historyBlockLeft{
  margin-right: 60px;
}

.infoBlock{
  margin-top: 160px;
}
.infoBlockFlex{
  display: flex;
}
.infoBlockLeft{
  width: calc(50% - 30px);
  margin-right: 30px;
}
.infoBlockRight {
  width: calc(50% - 30px);
  margin-left: 30px;
}
.infoBlockFlexM80{
  margin-top: 80px;
}
.link{
  color: #135AFF;
  text-decoration: none;
  font-size: 16px;
  font-weight: 500;
  font-family: 'Montserrat';
  display: flex;
  align-items: center;
}
@media (min-width:1340px) {
  .link {
    font-size: 18px;
  }
}
.link svg {
  margin-right: 20px;
}
.footer{
  margin-top: 160px;
  width: 100%;
  height: 136px;
  background-image: url('./images/background-footer.png');
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  align-items: center;
}
.footerFlex{
  display: flex;
  justify-content: space-between;
  color: #FFFFFF;
  font-family: 'Montserrat';
  font-weight: 700;
  text-transform: uppercase;
  font-size: 12px;
}
.rightCopyright svg{
  margin-right: 10px;
  margin-bottom: -3px;
}
.rightCopyright{
  color: #FFFFFF;
  font-family: 'Montserrat';
  font-weight: 700;
  text-transform: uppercase;
  font-size: 12px;
}
.overflow{
  overflow-x: hidden;
}
.startButtonBlue{
  font-family: 'Montserrat';
  font-weight: 700;
  font-size: 14px;
  background: #135AFF;
  padding: 18px 32px;
  color: #FFFFFF;
  border-radius: 56px;
  text-decoration: none;
  position: relative;
  margin: 0 auto;
  margin-top:80px;
  margin-bottom: 160px;
  display: block;
  width: 252px;
}
@media (min-width:1340px) {
  .startButtonBlue {
    font-size: 16px;
  }
}