.DragContainer{
  height: 88%;
  width: calc(100% - 48px);
  margin: 0 24px;
  position: absolute;
  border-radius: 8px;
  background-color: var(--mion-message-color);
  padding: 12px;
  bottom: 9%;
  text-align: center;
  z-index: 9;
}
@media(min-width: 1600px) {
  .DragContainer {
    bottom: 7%;
    height: 90%;
  }
}
@media(min-width: 2700px) {
  .DragContainer {
    bottom: 6%;
    height: 90%;
  }
}
.DragBordered{
  height: 100%;
  border: 2px dashed var(--cabinet-files-border-color); 
  display: flex;
  flex-direction: column; 
  justify-content: center;
}
.DragText{
  font-size: 16px;
  font-weight: 400;
  color: var(--chat-message-text-color);
  margin-bottom: 10px;
}
.DragFormat{
  font-size: 14px;
  font-weight: 400;
  color: var(--cabinet-subtext-color)
}
