.chat {
  width: 100%;
  height: 100%;
  background-color: var(--chat-background-color);
  font-family: Inter, serif;
  font-weight: 400;
  display: block;
  margin-right: 32px;
  border-radius: 8px;
  position: relative;
}
.chatScroller {
  height: 89%;
  overflow: auto;
  border-radius: 8px;
}
.inputBar {
  /* height: 119px; */
  height: 8%;
  /* background-color: #000227; */
  background-color: var(--chat-background-color);
  display: flex;
  flex-direction: row;
  position: relative;
}

.inputField {
  position: absolute;
  width: calc(100% - 48px);
  bottom: 0;
  background-color: var(--mion-message-color);
  min-height: 43px;
  margin: 0 24px;
  padding: 11px 50px;
  padding-left: 138px;
  border-radius: 8px;
  cursor: text;
  overflow-x: auto;
  user-select: text; 
  color: var(--color-control-typo-default);
  caret-color: var(--cabinet-subtext-color);
  max-height: 300px;
}

.inputField::before{
  content: attr(data-placeholder);
  position: absolute;
  left: 130px;
  color: var(--cabinet-subtext2-color);
  font-size: 16px;
  display: block;
  pointer-events: none;
  position: absolute;
  max-width: 100%;
  padding-right: 0.5rem;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  opacity: 1;
}
.inputField.onInput::before{
  opacity: 0;
}

